const SListExamsScreenTab = ({navigation}: any) => {
  const {listStudentExams, loading, getListStudentExams, getListExamResult} =
    useExamResultStore();
  const {user, accessToken, refreshToken} = useAuthStore();

  const theme = useTheme();
  useEffect(() => {
    if (listStudentExams.length === 0) {
      getListStudentExams(user.preferred_username);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const onRefresh = () => {
    getListStudentExams(user.preferred_username);
  };

  const onViewExamResult = (maDeThi: string) => {
    console.log(maDeThi);
    getListExamResult(user.preferred_username, maDeThi);
    navigation.push('ListExamResultScreen');
  };

  const renderItem = ({item}: {item: IStudentExams}) => (
    <List.Item
      title={item.noiDungDeThi}
      description={
        <View style={{display: 'flex', flexDirection: 'column'}}>
          <View
            style={{
              paddingTop: 8,
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
              gap: 4,
            }}>
            <NumberQuizIcon
              width={16}
              height={16}
              color={theme.colors.secondary}
            />
            <Text style={{color: theme.colors.secondary}}>
              {item.soCau} câu -
            </Text>
            <ClockIcon color={theme.colors.secondary} width={16} height={16} />
            <Text style={{color: theme.colors.secondary}}>
              {item.thoiGian.trim()} phút
            </Text>
          </View>
          <View
            style={{
              paddingTop: 8,
              display: 'flex',
              flexDirection: 'row',
              alignItems: 'center',
              gap: 4,
            }}>
            <CalendarIcon
              width={16}
              height={16}
              color={theme.colors.secondary}
            />
            <Text style={{color: theme.colors.secondary}}>
              {formatDateTime(item.ngayBatDau)}
              {' - '}
              {formatDateTime(item.ngayKetThuc)}
            </Text>
          </View>
        </View>
      }
      style={{
        borderBottomColor: theme.colors.elevation.level5,
        borderBottomWidth: 1,
      }}
      // left={() => (
      //   <View style={styles.listIcon} pointerEvents="box-none">
      //     <MathIcon width={48} height={48} />
      //   </View>
      // )}
      right={() => (
        <View
          style={{
            paddingTop: 8,
            display: 'flex',
            flexDirection: 'column',
            gap: 8,
          }}>
          <Button
            onPress={() => {
              const baseUrl = 'https://smarttest.pro';
              const url = `/${buildLink('test-taking/exam/:id', {
                id: item.maDeThi,
              })}`;
              const uri = `${baseUrl}/thanh-vien/dang-nhap?return_to=${encodeURI(
                url,
              )}&accessToken=${encodeURI(
                accessToken || '',
              )}&refreshToken=${encodeURI(refreshToken || '')}`;
              navigation.push('TestExamWebScreen', {
                uri,
              });
            }}>
            Thi lại
          </Button>
          <Button onPress={() => onViewExamResult(item.maDeThi)}>
            Kết quả
          </Button>
        </View>
      )}
    />
  );

  return (
    <View style={styles.container}>
      <FlatList
        data={listStudentExams.sort(
          (a, b) =>
            new Date(b.ngayBatDau).getTime() - new Date(a.ngayBatDau).getTime(),
        )}
        renderItem={renderItem}
        keyExtractor={item => item.maDeThi}
        refreshControl={
          <RefreshControl
            refreshing={loading === 'loading'}
            onRefresh={onRefresh}
          />
        }
      />
    </View>
  );
};

