const SListExamsScreen = ({navigation}: any) => {
  // const insets = useSafeAreaInsets();
  const layout = useWindowDimensions();
  const theme = useTheme();
  const {getListStudentExams, filterListStudentExams, saveLoading} =
    useExamResultStore();
  const {user} = useAuthStore();
  const [searchQuery, setSearchQuery] = React.useState('');

  const [index, setIndex] = React.useState(0);

  const [routes] = React.useState([
    {key: 'first', title: 'Danh sách'},
    {key: 'second', title: 'Chờ thi'},
  ]);

  const renderScene = ({route}: {route: {key: string; title: string}}) => {
    switch (route.key) {
      case 'first':
        return <SListExamsScreenTab navigation={navigation} />;
      case 'second':
        return <SListExamsWaitScreenTab navigation={navigation} />;
      default:
        return null;
    }
  };

  const onSearch = (noidung: string) => {
    if (noidung.length > 0) {
      filterListStudentExams(noidung);
    } else {
      getListStudentExams(user.preferred_username);
    }
  };

  const renderTabBar = (props: any) => (
    <TabBar
      {...props}
      indicatorStyle={{backgroundColor: 'white'}}
      style={{backgroundColor: theme.colors.primary}}
    />
  );

  return (
    <View style={{flex: 1}}>
      <Appbar.Header style={{backgroundColor: theme.colors.primary}}>
        <Appbar.BackAction
          onPress={() => navigation.navigate('SmartTestHomeScreen')}
        />
        <Appbar.Content title="Bài thi của bạn" color="white" />
      </Appbar.Header>
      <Searchbar
        placeholder="Tên đề thi"
        onChangeText={setSearchQuery}
        value={searchQuery}
        onIconPress={() => onSearch(searchQuery)}
        onClearIconPress={() => getListStudentExams(user.preferred_username)}
        mode="view"
        style={{height: 35}}
        inputStyle={{
          minHeight: 0, // Add this
        }}
      />
      <TabView
        // style={{paddingTop: insets.top, backgroundColor: theme.colors.primary}}
        navigationState={{index, routes}}
        renderScene={renderScene}
        renderTabBar={renderTabBar}
        onIndexChange={setIndex}
        initialLayout={{width: layout.width}}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    // paddingHorizontal: 4,
  },
  listIcon: {
    paddingLeft: 12,
    alignItems: 'center',
    justifyContent: 'center',
  },
  appBar: {
    height: 35, // Adjust the height as per your requirement
  },
});

export default SListExamsScreen;
