import type {
  IClassroom,
  IStudentClassroom,
  Loading,
  AnyObj,
} from '@smarttestpro/smart-common';
import {create, Api, ApiClient} from '@smarttestpro/smart-common';

const SmartTestClient = ApiClient.getInstance('smart-test');
SmartTestClient.setEndpoint('https://api.smarttest.pro/api');

type ClassroomStore = {
  classId: string;
  listClassroom: IClassroom[];
  listStudentClassroom: IStudentClassroom[];
  classroom: IClassroom;
  loading: Loading;
  saveLoading: Loading;
};

type ClassroomStoreAction = {
  setListClassroom: () => void;
  setListStudentClassroom: (truongLopId: string) => void;
  getClassroom: (truongLopId: string) => void;
};

const initValue: ClassroomStore = {
  classId: '',
  listClassroom: [],
  listStudentClassroom: [],
  classroom: {} as AnyObj,
  loading: 'none',
  saveLoading: 'none',
};

export const useClassroomStore = create<
  ClassroomStore & ClassroomStoreAction
>()((set, _get) => ({
  ...initValue,
  setListClassroom: async () => {
    const result = await Api.ClassRoomApi.getClassroomBySub();
    set({listClassroom: result.data});
  },
  setListStudentClassroom: async (truongLopId: string) => {
    const result = await Api.ClassRoomApi.getStudentByClassId(truongLopId);
    set({listStudentClassroom: result.data});
  },
  getClassroom: async (truongLopId: string) => {
    set({classId: truongLopId, loading: 'loading'});
    try {
      const result = await Api.ClassRoomApi.getClassroomById(truongLopId);

      set({classroom: result.data, loading: 'success'});
    } catch (error) {
      set({loading: 'error'});
    }
  },
}));
